<template>
  <el-card>
    <el-row>
      <el-col :span="10">
        当前店铺：
        <el-select v-model="value" placeholder="请选择">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
            :style="{'min-width': minWidth + 'px'}"
          ></el-option>
        </el-select>
      </el-col>
      <el-col :span="10">
        优惠状态：
        <el-select v-model="statusValue" placeholder="请选择">
          <el-option
            v-for="item in statusOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value"
            :style="{'min-width': minWidth + 'px'}"
          ></el-option>
        </el-select>
      </el-col>
      <el-col :span="2">
          <el-button type="primary" icon="el-icon-circle-plus-outline">添加活动</el-button>
      </el-col>
    </el-row>
    <el-row class="mtb15">
      <el-table
        :data="tableData"
        border
        stripe
        class="w24"
      >
        <el-table-column prop="discountActivityId" label="优惠活动ID" width="170" align="center"></el-table-column>
        <el-table-column prop="discountName" label="优惠名称" width="120" align="center"></el-table-column>
        <el-table-column prop="beginTime" label="开始时间" width="160" align="center"></el-table-column>
        <el-table-column prop="overTime" label="结束时间" width="160" align="center"></el-table-column>
        <el-table-column prop="creator" label="创建人" align="center"></el-table-column>
        <el-table-column prop="discountType" label="优惠类型" align="center"></el-table-column>
        <el-table-column prop="discountStatus" label="优惠状态" align="center"></el-table-column>
        <el-table-column prop="creationTime" label="创建时间" width="160" align="center"></el-table-column>
        <el-table-column label="操作" width="150" align="center">
          <template slot-scope="scope">
            <el-tooltip content="详情" placement="top">
              <el-button type="warning" size="mini" icon="el-icon-view" @click="detailClick(scope.$index , scope.row )"></el-button>
            </el-tooltip>
            <el-tooltip content="取消" placement="top">
              <el-button type="danger" size="mini" icon="el-icon-circle-close"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
    </el-row>
  </el-card>
</template>

<script>
import {mixin} from '../../mixins/resize'
export default {
  mixins: [mixin],
  data() {
    return {
      tableData: [],
      options: [
        {
          value: "选项1",
          label: "紫荆之巅"
        },
        {
          value: "选项2",
          label: "佛系店铺"
        }
      ],
      value: "紫荆之巅",
      statusOptions: [
        {
            value: "选项一",
            label: "未生效"
        },
        {
            value: "选项二",
            label: "已生效"
        },
        {
            value: "选项三",
            label: "所有"
        }
      ],
      statusValue: ""
    };
  },
  methods: {
    detailClick() {
      console.log(2);
    }
  },

  async mounted() {
    this.tableData = await this.$get(
      "../../../../static/discountActivity.json"
    );
  }
};
</script>


